import { Quest, GameState } from './types';
import { ECONOMY, FAITH, SCIENCE } from './app/constants';

export const QUESTS: Record<string, Quest> = {
  first_upgrade: {
    id: 'first_upgrade',
    name: '  ',
    description: ' 10     ',
    progress: 0,
    maxProgress: 10,
    isCompleted: false,
    isVisible: true,
    checkProgress: (state: GameState) => {
      //      ,   10
      return Math.min(state.total, 10);
    },
    checkVisibility: (state: GameState) => {
      //      
      return !!state.selectedCountry && state.stage >= 1;
    },
    checkCompletion: (state: GameState) => {
      //  ,    
      return state.upgrades.some(upgrade => upgrade.level > 0);
    }
  },

  develop_sphere: {
    id: 'develop_sphere',
    name: '    ',
    description: ' 10       ',
    progress: 0,
    maxProgress: 10,
    isCompleted: false,
    isVisible: false,
    checkProgress: (state: GameState) => {
      //  -      
      const maxSphereResources = Math.max(
        state.spheres[ECONOMY].resources,
        state.spheres[FAITH].resources,
        state.spheres[SCIENCE].resources
      );
      return Math.min(maxSphereResources, 10);
    },
    checkVisibility: (state: GameState) => {
      //        100
      return state.total > 100;
    },
    checkCompletion: (state: GameState) => {
      //  ,      10 
      return state.spheres[ECONOMY].resources > 10 ||
             state.spheres[FAITH].resources > 10 ||
             state.spheres[SCIENCE].resources > 10;
    }
  },

  expand_borders: {
    id: 'expand_borders',
    name: ' ',
    description: '  ',
    progress: 0,
    maxProgress: 1,
    isCompleted: false,
    isVisible: false,
    checkProgress: (state: GameState) => {
      //  0     , 1  
      return state.conqueredCountries.length > 1 ? 1 : 0;
    },
    checkVisibility: (state: GameState) => {
      //           
      //          
      return state.mapEvents.length > 0 || state.total > 500;
    },
    checkCompletion: (state: GameState) => {
      //  ,     
      return state.conqueredCountries.length > 1;
    }
  },

  ship_shields: {
    id: 'ship_shields',
    name: '  ',
    description: '     ',
    progress: 0,
    maxProgress: 1,
    isCompleted: false,
    isVisible: false,
    checkProgress: (state: GameState) => {
      //  1    
      return state.cosmicCivilization.shipShield ? 1 : 0;
    },
    checkVisibility: (state: GameState) => {
      //       2 
      return state.stage >= 2;
    },
    checkCompletion: (state: GameState) => {
      //  ,   secondStage   shipShield
      return state.secondStage.includes('shipShield') || state.cosmicCivilization.shipShield;
    }
  }
};

//        
export const getAllQuests = (): Quest[] => {
  return Object.values(QUESTS);
};

//      ID
export const getQuestById = (id: string): Quest | undefined => {
  return QUESTS[id];
}; 